.header {
  position: relative;
  margin-top: -10px;
}
.header img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.header .header-disc {
  text-align: center;
  margin: 10px auto;
  color: #c9d1d9;
}
@keyframes blinker {
  40% {
    opacity: 0.5;
  }
}
.header .items-click {
  animation: blinker 2s linear infinite;
  color: #1c87c9;
  font-weight: 600;
}

@media screen and (min-width: 1290px) {
  .my-container {
    position: absolute;
    top: 6vh;
    right: 7vw;
    font-size: 2rem;
  }
}
@media screen and (max-width: 1290px) and (min-width: 810px) {
  .my-container {
    position: absolute;
    top: 3vh;
    right: 5vw;
    font-size: 1.5rem;
  }
  .my-container h1 {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 810px) and (min-width: 530px) {
  .my-container {
    position: absolute;
    top: 2vh;
    right: 2vw;
    font-size: 1.15rem;
  }
  .my-container h1 {
    font-size: 1.15rem;
  }
}
@media screen and (max-width: 530px) {
  .my-container {
    display: none;
  }
}